<template>
    <div>
        <BaseInputText
                v-model.trim="newTodoText"
                placeholder="请输入你要做的事..."
                @keydown.enter="addTodo"
        />
        <ol v-if="todos.length" class="list">
            <TodoListItem
                    v-for="todo in todos"
                    :key="todo.id"
                    :todo="todo"
                    @remove="removeTodo"
            />
        </ol>
        <p v-else>
            待办事项为空，请在上方添加要做的事情。
        </p>
    </div>
</template>

<script>
    import BaseInputText from './BaseInputText.vue'
    import TodoListItem from './TodoListItem.vue'

    let nextTodoId = 1

    export default {
        components: {
            BaseInputText, TodoListItem
        },
        data () {
            return {
                newTodoText: '',
                todos: [
                    {
                        id: nextTodoId++,
                        text: '打篮球'
                    },
                    {
                        id: nextTodoId++,
                        text: '打棒球'
                    },
                    {
                        id: nextTodoId++,
                        text: '打羽毛球'
                    },
                    {
                        id: nextTodoId++,
                        text: '打乒乓球'
                    }
                ]
            }
        },
        methods: {
            addTodo () {
                const trimmedText = this.newTodoText
                if (trimmedText) {
                    this.todos.push({
                        id: nextTodoId++,
                        text: trimmedText
                    })
                    this.newTodoText = ''
                }
            },
            removeTodo (idToRemove) {
                this.todos = this.todos.filter(todo => {
                    return todo.id !== idToRemove
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .list {
        padding-left: 20px;
        text-align: center;
    }
</style>