<template>
    <li class="item">
        {{ todo.text }}
        <button @click="$emit('remove', todo.id)" class="remove">
            X
        </button>
    </li>
</template>

<script>
    export default {
        props: {
            todo: {
                type: Object,
                required: true
            }
        }
    }
</script>

<style lang="scss" scoped>
    .item {
        background-color: #ddd;
        margin-bottom: 5px;
    }

    .remove {
        float: right;
    }
</style>
